@mixin flex-display {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  -moz-box-orient: horizontal;
  -moz-box-direction: normal;
  -ms-flex-direction: row;
          flex-direction: row;
}
// Flexbox centering
.flex-center {
  @include flex-display;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.flex-start {
  @include flex-display;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -moz-box-align: center;
  -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
  -moz-box-pack: start;
  -ms-flex-pack: start;
          justify-content: flex-start;
}
.flex-end {
  @include flex-display;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -moz-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: end;
  -webkit-justify-content: flex-end;
  -moz-box-pack: end;
  -ms-flex-pack: end;
          justify-content: flex-end;
}

// Padding & margins
.mb-1 {
  margin-bottom: 1rem;
}
.mb-2 {
  margin-bottom: 2rem;
}
.mb-3 {
  margin-bottom: 3rem;
}
.mb-4 {
  margin-bottom: 4rem;
}
.mt-2 {
  margin-top: 2rem;
}
.mt-3 {
  margin-top: 3rem;
}
.mt-4 {
  margin-top: 4rem;
}

.padding-x-sm {
  padding-left: 2%;
  padding-right: 2%;
}
.padding-x-md {
  padding-left: 4%;
  padding-right: 4%;
}
.padding-x-lg {
  padding-left: 7%;
  padding-right: 7%;
}
.padding-y-sm {
  padding-top: 2rem;
  padding-bottom: 2rem;
}
.padding-y-md {
  padding-top: 3.5rem;
  padding-bottom: 3.5rem;
}
.padding-y-lg {
  padding-top: 5rem;
  padding-bottom: 5rem;
}

// Background Classes
.bg-pink {
  background-color: $secondary-color;
  color: $primary-color;
}

.bg-orange {
  background-color: $secondary-color;
  color: $primary-color;
}

.bg-blue {
  color: whitesmoke;
  background-color: $accent-color;
}

// Typography
.text-dark {
  color: $primary-color !important;
}
.text-light {
  color: $secondary-color !important;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-justify {
  text-align: justify;
}

.font-light {
  font-weight: 200;
}
.font-bold {
  font-weight: 900;
}
.impact {
  font-size: 10rem;
  text-transform: lowercase;
  font-weight: 900;
  color: $primary-color;
}

// Images & containers

.responsive-img {
  width: 100%;
  height: 100%;
   -o-object-fit       : cover;
     object-fit        : cover;
  -o-object-position   : center;
     object-position   : center;
}

.small-divider {
  height: 10px;
  background: $accent-color;
  width: 100px;
  border: none;
}