// Main Stylesheet

// Colors
$primary-color          : #d74600;
$secondary-color        : #ffcbcb;
$accent-color           : #6555ff;
// $shade-color            : ;
// $complementary-color    : ;

// Fonts
$body-font        : "Sofia Pro", sans-serif;
$heading-font     : "Sofia Pro", sans-serif;
$body-font-size   : 1.1rem;
$base-line-height : 1.8;


@import
  "_resets",
  "_utility",
  "_universal",
  "_mixins",
  "_buttons",
  "_nav",
  "_footer",
  "_header",
  "_team",
  "_sliders",
  "_sections",
  "_media-queries";

.back-to-top {
  $axis: 60px;
  cursor: pointer;
  position: fixed;
  transform: translateY(250%);
  z-index: 10000;
  bottom: 2rem;
  right: 2rem;
  border: none;
  background-color: $primary-color;
  width: $axis;
  height: $axis;
  border-radius: 50%;
  &::before {
    position: absolute;
    top: -2rem;
    left: 50%;
    transform: translateX(-50%);
    content: "topo";

  }
  &::after {
    content: "\261D";
    font-size: 2rem;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 100001;    
  }
}



