.desktop-nav {
  padding: 3rem 0;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  .nav-link {
    cursor: pointer;
    text-transform: uppercase;
    font-size: 1.5rem;
    writing-mode: vertical-rl;
    list-style: none;
    text-orientation: sideways-right;
    padding: 0.3rem 0;
    margin-bottom: 2rem;
    a {
      color: $primary-color;
      @include transition;
      &:hover,
      &:focus {
        color: $accent-color;
        @include transition;
      }
    }
      &:last-of-type {
      margin-bottom: none;
    }
  }
}

// Dropdown menus

.dropdown {
  position    : relative;
  display     : inline-block;
  button {
    background-color: unset;
    border: none;
  } 
}
.dropdown-content {
  display     : none;
  padding     : 1.5rem 0.7rem;
  a {
    padding   : 0.5rem;
    // border-bottom: 1px solid;
  }
  li {
    line-height: unset;
    list-style: none;
  }
}
.dropdown:hover,
.dropdown:focus-within {
  background-color: $primary-color;
  color: whitesmoke;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  .dropdown-content {
    display   : block;
    position  : absolute;
    z-index   : 10000;
    width     : 83%;
    right     : 0;
    top       : 0;
    background-color: $primary-color;
    // @include transition;
    a {
      font-size: 0.9rem;
      padding: 0.8rem 0.3rem;
      text-transform: uppercase;
      color: whitesmoke;
    }
  }
}


#pages-header {
  .nav-title,
  .nav-link {
    color: $accent-color;
  }
  .nav-social-link svg {
    fill: $accent-color;
  }
